import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { SneheshVbKollaraComponent } from './snehesh-vb-kollara/snehesh-vb-kollara.component';
import { StoryMyInnocenceComponent } from './story-my-innocence/story-my-innocence.component';
const routes: Routes = [
  {path:'', component: HomeComponent, pathMatch: 'full'},
  {path: "story-my-innocence", component: StoryMyInnocenceComponent},
  {path: 'snehesh-vb-kollara', component: SneheshVbKollaraComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

 export const RoutingComponents = [
  HomeComponent, SneheshVbKollaraComponent, StoryMyInnocenceComponent
]